import React, {useState, useContext, useEffect}  from 'react';
import Layaout from '../components/Layaout/layaout'
import CardCultivo from '../components/Dinamicos/cardCultivo'

import dynamic from "next/dynamic";
import Router ,{useRouter} from 'next/router'
const LoginPage = dynamic(() => import("./login"));
import Link from 'next/link'
import {FirebaseContext} from '../firebase' // context

const Perfil = () => {
  const [plantas, setPlantas] = useState([]);
  const {firebase} = useContext(FirebaseContext);
  useEffect(() => {
    const obtenerPlantas = () =>{
        firebase.db.collection('plantas').where("medicinales", '==', true).onSnapshot(manejarSnapShot);
    }
     obtenerPlantas();
  }, []);
  // funcion que manipula el snapshot
  function manejarSnapShot (snapshot){
    const plantas = snapshot.docs.map(doc => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    // enviar el resultado de la conultaal state
    setPlantas(plantas);
    console.log(plantas);
  }

    return (
        <div>  
         
        </div>
    )
    
}

export default Perfil